@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;
  width: 100vw;
  max-width: 500px;

  .title {
    @include proxima(22px, 600, colors.$foreground_dark);
    padding-bottom: 1em;
  }

  .form-row {
    &__label {
      @include proxima(16px, 400, colors.$foreground_light-dark);
      margin-bottom: 0.2em;
      display: flex;
      align-items: center;
    }

    &__subscript {
      height: 22px;

      & > .mat-mdc-form-field-error {
        font-size: 12px;
        //position: relative;
        //bottom: 6px;
      }
    }

    &:not(:last-child) {
      margin-bottom: 0.5em;
    }

    ::ng-deep {
      --mdc-outlined-text-field-disabled-input-text-color: var(--mdc-outlined-text-field-input-text-color);
    }
  }

  .fields-list {

    &__item {
      display: flex;
      flex-wrap: wrap;
      padding: 14px;
      background-color: #f2f2f2;
      gap: 10px;

      &-section {
        display: flex;
        align-items: center;
        gap: 10px;

        &:first-child {
          flex-grow: 1;
          flex-shrink: 0;
          padding: 0 8px;
          background-color: #FFFFFF;
        }

      }

      &-text {
        @include proxima(16px, 600, colors.$foreground_dark);
      }

      &:not(:last-child) {
        border-bottom: 1px solid #E8E8E8;
      }

      &:last-child {
        margin-bottom: 10px;
      }
    }
  }

  .mat-mdc-card {
    box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
  }

  .button {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
